namespace server {
    export class gameServer {
        /**
         * 消息模式
         */
        mode: string

        constructor() {
            this.mode = 'async'
        }

        /**
         * 初始化游戏数据
         */
        onInitGameData() {
            return {}
        }

        /**
         * 监听客户端数据
         */
        onClientData(args: any) {
            args.SDK.logger.debug('监听客户端数据')
            //发送消息给客户端
            args.SDK.sendData({ playerIdList: [], data: { msg: 'hello' } })
            args.SDK.exitAction()
        }

        /**
         * 监听加房广播
         */
        onJoinRoom(args: any) {
            args.SDK.logger.debug('监听加房广播')
        }

        /**
         * 监听创建房间广播
         */
        onCreateRoom(args: any) {
            args.SDK.logger.debug('监听创建房间广播')
        }

        /**
         * 监听退房广播
         */
        onLeaveRoom(args: any) {
            args.SDK.logger.debug('监听退房广播')
        }

        /**
         * 监听玩家被移除广播
         */
        onRemovePlayer(args: any) {
            args.SDK.logger.debug('监听退房广播')
        }

        /**
         * 监听房间销毁广播
         */
        onDestroyRoom(args: any) {
            args.SDK.logger.debug('监听房间销毁广播')
        }

        /**
         * 监听修改房间属性广播
         */
        onChangeRoom(args: any) {
            args.SDK.logger.debug('监听修改房间属性广播')
        }

        /**
         * 监听修改玩家自定义状态广播
         */
        onChangeCustomPlayerStatus(args: any) {
            args.SDK.logger.debug('监听修改玩家自定义状态广播')
        }

        /**
         * 监听玩家网络状态变化广播
         */
        onChangedPlayerNetworkState(args: any) {
            args.SDK.logger.debug('监听修改玩家自定义状态广播')
        }

        /**
         * 监听开始/停止帧同步广播
         */
        onMessageRelay(args: any) {
            args.SDK.logger.debug('监听开始/停止帧同步广播')
        }
    }
}
//将 gameServer 类引用置于 exports 上
var exports: any;
exports.gameServer = server.gameServer;